html, body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Raleway', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: -webkit-linear-gradient(315deg, #552299 0%, #222244 100%);
  background: linear-gradient(135deg, #552299 0%, #222244 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content > div:nth-child(1) {
  background: -webkit-linear-gradient(315deg, #ff0099 0%, #9966ff 100%);
  background: linear-gradient(135deg, #ff0099 0%, #9966ff 100%);
}
.content > div:nth-child(2) {
  background: -webkit-linear-gradient(315deg, #0066ff 0%, #009966 100%);
  background: linear-gradient(135deg, #0066ff 0%, #009966 100%);
}
.content > div:nth-child(3) {
  background: -webkit-linear-gradient(315deg, #ff0022 0%, #ff6600 100%);
  background: linear-gradient(135deg, #ff0022 0%, #ff6600 100%);
}
.content > div:nth-child(4) {
  background: -webkit-linear-gradient(315deg, #ffff33 0%, #ff6600 100%);
  background: linear-gradient(135deg, #ffff33 0%, #ff6600 100%);
}

.flex-container {
  background-color: green;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.flex-container > div:nth-child(1){
  background-color: red;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 50px;
}

.flex-container > div:nth-child(2){
  background-color: pink;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 50px;
}

.flex-container > div:nth-child(3){
  background-color: violet;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 50px;
  text-align: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.flex-container > div:nth-child(4){
  background-color: sandybrown;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
